export const bestFitRow = (containerWidth, wall) => {

    const { ThemeStyle, CardStyle } = wall
    try {
        const { Posts } = store.getState().appData;
        const postLength = Posts.Posts.length;

        const minWidth = Math.floor(containerWidth / CardStyle.minimumWidth);
        return ThemeStyle.row === 1 && postLength >= minWidth
            ? ThemeStyle.column.desktop
            : postLength;
    } catch {
        return ThemeStyle.column.desktop;
    }
};

const getDividedValue = (v1, v2) => v1 / v2;

const postWidthAdjustSpacing = (c, r, w, p) => {
    const spacing = w % p; // Remaining space after dividing container width by post width
    const adjustSpacing = spacing / r; // Adjust spacing per row
    const adjustedWidth = c >= 1 ? p + adjustSpacing : p - adjustSpacing; // Adjust post width
    return { spacing, width: adjustedWidth, adjustSpacing };
};

const defaultColumn = (v) => {
    if (v < 575.98) return 1;
    if (v < 767.98) return 2;
    if (v < 991.98) return 3;
    if (v < 1199.98) return 4;
    return 5;
};

const postSizeByColumn = (containerWidth, columns) => (containerWidth / columns) * 100 / containerWidth;

export const GET_POST_SIZE = (containerWidth, wall) => {
    const { ThemeInfo, ThemeStyle, CardStyle } = wall;
    const { desktop, mobile } = ThemeStyle.column;
    const themeId = [3, 4, 5, 20, 19, 62, 70, 64, 79, 83]; // Example theme IDs
    
    // Function to get the number of columns based on conditions
    // const getNumberOfColumns = () => {
    //     if (ThemeStyle.bestFit === 1 && desktop === 0) {
    //         // Replace `bestFitRow` with your actual function
    //         return bestFitRow(containerWidth, wall);
    //     }
    //     return parseInt(window.innerWidth < 768 ? mobile : desktop);
    // };
    // Handle specific theme IDs
    if (themeId.includes(ThemeInfo.id)) {
        const columns = desktop || mobile ? parseInt(containerWidth < 768 ? mobile : desktop) : Math.floor(containerWidth / CardStyle.minimumWidth);
        const singlePostWidth = postSizeByColumn(containerWidth, columns);
        return { adjustWidth: singlePostWidth, cardNumber : columns };
    }

    // Function to calculate adjusted width and number of cards
    const calculateWidthAndColumns = (singlePostWidth) => {
        const cardNumber = getDividedValue(containerWidth, singlePostWidth);
        const { width: adjustWidth } = postWidthAdjustSpacing(cardNumber, Math.trunc(cardNumber), containerWidth, singlePostWidth);
        return { adjustWidth: adjustWidth - 0.8, cardNumber: Math.trunc(cardNumber) };
    };
    // Handle theme ID 50
    if (ThemeInfo.id === 50) {
        const numberOfColumns = ThemeStyle.column.desktop === 0 ? defaultColumn(containerWidth) : parseInt(ThemeStyle.column.desktop);
        return containerWidth > CardStyle.minimumWidth
            ? calculateWidthAndColumns(containerWidth / numberOfColumns - 10)
            : { adjustWidth: containerWidth, cardNumber: 1 };
    }

    // Handle theme ID 67
    if (ThemeInfo.id === 67) {
        const columnMap = { 8: 4, 12: 4, 18: 6, 32: 8 };
        const numberOfColumns = columnMap[desktop] || 4;
        return calculateWidthAndColumns(containerWidth / numberOfColumns);
    }

    // Default case
    if (containerWidth > CardStyle.minimumWidth) {
        return calculateWidthAndColumns(CardStyle.minimumWidth);
    }

    // Fallback for very small container widths
    return { adjustWidth: containerWidth, cardNumber: 1 };
};


export const getWallID = () => {

    const urlIncludes = (keywords) => keywords.some((keyword) => window.location.href?.includes(keyword));
    const queryWallId = getQueryString(window.location.href, 'wall_id');

    if (queryWallId) return queryWallId;

    const wall_Id = window.wall_Id

    if (document.getElementsByClassName("wall-editor")?.length && wall_Id) return wall_Id;
    if (urlIncludes(["localhost"]) && wall_Id) return wall_Id;

    if (urlIncludes(["widget-lite"])) {
        const baseURL = `https://${Pwidget}-lite.${tbox}/`;
        const wallPath = window.location.href.split(baseURL)?.[1];
        return wallPath?.split("?")?.[0] || wall_Id;
    }

    if (urlIncludes([`${test}${tbox}/embed-${Pwidget}/lite`])) {
        const container = document.getElementsByClassName("taggbox");
        for (const element of container) {
            const { isLoad, renderId, widgetId } = element.dataset
            if (isLoad === "0") {
                document
                    .getElementById(`co_${renderId}`)
                    ?.setAttribute("data-is-load", "1");
                return widgetId;
            }
        }
    }

    const platformURLs = [
        `${Pdisplay}-${test}${sw}`,
        `${Pdisplay}.${sw}`,
        `${Pwidget}.${sw}`,
        `${Pwidget}-${test}${tagembed}`,
        `${Pwidget}s.${tagembed}`,
        `${Pwidget}.${tagembed}`,
        `${Pwidget}-${test}${ts}`,
        `${Pwidget}.${ts}`,
        `${test}${ts}/w/`,
        `${test}${ts}/s/`,
    ];

    if (urlIncludes(platformURLs)) {
        const splitURL = platformURLs.find((key) => wUrl(key)) || `${Pwidget}.${tbox}/`;
        const baseUrl = `${http}${splitURL.includes(`${test}${ts}`) ? splitURL : `${splitURL}/`}`;
        const wallPath = window.location.href.split(baseUrl)?.[1];
        return wallPath?.split("?")?.[0] || window.wall_Id;
    }

    const socialWallClass = document.getElementsByClassName(tagSocialWall);
    const tagClassValue = document.getElementsByClassName(tagClass);
    const container = socialWallClass?.length ? socialWallClass : tagClassValue;


    for (const element of container) {

        const { isLoad, wallId, widgetId, renderId } = element.dataset

        if (isLoad === "0") {
            document
                .getElementById(`co_${renderId}`)
                ?.setAttribute("data-is-load", "1");
            return socialWallClass?.length
                ? wallId
                : isTagShop
                    ? wallId
                    : widgetId;
        }
    }

    return null;
};

