export function getRootElements() {
    const taggbox_main = document.getElementById("taggbox_main");
    if (!taggbox_main) extendsGetRenderContainer();
    // Get the elements (return an array)
    return taggbox_main
        ? [taggbox_main]
        : Array.from(document.querySelectorAll(".tagshop-socialwall, .taggbox-socialwall"));
}


export const extendsGetRenderContainer = () => {
    try {
        var socialwall = document.getElementsByClassName(tagSocialWall).length
        var tagembed = document.getElementsByClassName(tagClass).length
        if (socialwall && tagembed) {
            if (document.getElementsByClassName(tagSocialWall)) {
                const tagembedSocial = document.getElementsByClassName(tagSocialWall)[0]
                if (tagembedSocial && tagembedSocial.getAttribute("data-render-id") && tagembedSocial.getAttribute("data-is-load") == "1") {
                    socialwall = 0
                }
            }
        }

        var container = socialwall && socialwall > 0 ? document.getElementsByClassName(tagSocialWall) : document.getElementsByClassName(tagClass);

        for (var i = 0; i < container.length; i++) {
            if (container[i].getAttribute("data-is-load") == "0") {
                if (container[i].getAttribute('data-render-id')) rID = container[i].getAttribute('data-render-id')
                return container[i].getAttribute("data-render-id")
            }
        }
    }
    catch (ex) {
        return document.getElementById('taggbox_main');
    }
}

export const getEmbed = ()=>{
    const isTagshop = document.getElementsByClassName("tagshop-socialwall")
        let containerElements = isTagshop?.length ? isTagshop : document.getElementsByClassName("taggbox-socialwall");
        return containerElements.length
}


export const getRenderContainer = () => {
    try {
        const isTagshop = document.getElementsByClassName("tagshop-socialwall")
        let containerElements = isTagshop?.length ? isTagshop : document.getElementsByClassName("taggbox-socialwall");

        if (containerElements.length) {

            for (let i = 0; i < containerElements.length; i++) {
                let element = containerElements[i];

                //  Skip already processed containers
                if (element.getAttribute("embed-is-load") === "1") {
                    continue;
                }

                //  Get the render container by `data-render-id`
                let renderId = element.getAttribute("data-render-id");
                if (renderId) {
                    rID = renderId
                    let targetContainer = document.getElementById(renderId);
                    if (targetContainer) {
                        //  Mark as processed
                        element.setAttribute("embed-is-load", "1");
                        return targetContainer; //  Return only one unprocessed container
                    }
                }
            }

            return null; //  No more containers left

        } else {
            return document.getElementById("taggbox_main"); // Default fallback
        }

    } catch (ex) {
        return document.getElementById("taggbox_main"); // Default fallback
    }
};


