export const getParameter = (n, t = window.location.href) => { n = n.replace(/[\[\]]/g, '\\$&'); const r = new RegExp('[?&]' + n + '(=([^&#]*)|&|#|$)'), e = r.exec(t); if (!e) return null; if (!e[2]) return ''; return decodeURIComponent(e[2].replace(/\+/g, ' ')); };

export const getCurrentTime = (isTime) => {
    const date = new Date();
    return [date.getMilliseconds(), date.getMinutes(), date.getHours(), date.getSeconds()][isTime - 1];
}


let postDetail = [];
let postId = [];
let listenerAdded = false;


const headers = new Headers({
    'Content-Type': 'application/json',
    'Authorization': `Bearer ${AUTH_TOKEN}`,
    'Productdomain': tbox
});

export const themeImpressionTracking = (ref) => {
    const { wall } = store.getState().appData;

    if (!listenerAdded) {
        window.addEventListener('beforeunload', () => {

            if (postDetail.length && ((isTagShop && !TagShopEditor) || (isTagbox && !window.editor))) {
                fetch(UPDATE_TRACKING, {
                    method: 'POST',
                    headers,
                    body: JSON.stringify({
                        action: isTagShop ? 3 : 2,
                        wall: isTagShop ? wall.ThemeInfo.webId : wall.ThemeInfo.wallId,
                        owner: wall.ThemeInfo.owner,
                        ...(isTagShop ? { model: dataSource === 'website' ? 'website' : 'wall' } : { posts: postDetail })
                    }),
                    keepalive: true
                });
            }
        });
        listenerAdded = true;
    }


    const observer = new IntersectionObserver(entries =>
        entries.forEach(({ isIntersecting, target }) => {
            const postIdAttr = target.getAttribute("post-id");
            if (isIntersecting && !postId.includes(postIdAttr)) {
                postDetail.push({ post: postIdAttr, feed: target.getAttribute("feed-id") });
                postId.push(postIdAttr);
                observer.unobserve(ref.current);
            }
        })
    );

    if (ref.current) observer.observe(ref.current);
};

export const isInstaUser = (str) => str === 'Instagram User';

